				"html": "<p><img src=\"//discourse-user-assets.s3.amazonaws.com/original/2X/a/a081f3fa5316b7d400a5e518bb0620eef64caa08.jpg\"></p>\n<h3>Explicación:</h3>\n<p>La función es simplemente una operación booleana. Es necesario que se devuelva true si el primer argumento termina con el segundo argumento. Esto significa que para problema de ejemplo <code>confirmEnding('Bastian', 'n');</code>, debe devolver true.</p>\n<h2>Pista: 1</h2>\n<p>Hecha un vistazo a como <code>substr()</code> funciona. Deberás intentar obtener los últimos X caracteres.</p>\n<h2>Pista: 2</h2>\n<p>Para obtener los últimos X caracteres deberás utilizar length() y convertirlo en número negativo.</p>\n<h2>Pista: 3</h2>\n<p>Comprueba que tienes la sintaxis de forma apropiada y que estés utilizando <code>===</code> para comparar.</p>\n<h2>¡Alerta de Spoiler!</h2>\n<p><img src=\"//discourse-user-assets.s3.amazonaws.com/original/2X/2/2d6c412a50797771301e7ceabd554cef4edcd74d.gif\" alt=\"687474703a2f2f7777772e796f75726472756d2e636f6d2f796f75726472756d2f696d616765732f323030372f31302f31302f7265645f7761726e696e675f7369676e5f322e676966.gif\"></p>\n<p><strong>¡Solución abajo!</strong></p>\n<h2>Solución del código:</h2>\n<pre><code>function confirmEnding(str, target) {\n  return str.substr(-target.length) === target;\n}\n</code></pre>\n<p><img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=2\" alt=\":rocket:\" title=\":rocket:\"> <a href=\"https://repl.it/CLjU/18\">Ejecutar código</a></p>\n<h2>Explicación del código:</h2>\n<p>Utilizamos subtring() con el valor negativo que nos devuelve la longitud de target. Podríamos utilizar -1 para obtener el último carácter pero si la longitud de target es en realidad mayor que uno entonces la función devolvería la información incorrecta. Luego retornamos el valor de su expresión boolanea.</p>\n<blockquote>\n<p><strong>NOTA:</strong> Por favor añade tu nombre de usuario solamente si has añadido <strong>contenido relevante</strong> al artículo. (Por favor no remuevas ningún nombre existente.)</p>\n</blockquote>",
